            <!-- <div class="crew-info-item crew-info-item4">
            <div class="target-power-box">
              <em class="left-text">目标功率</em>
              <span></span>
              <em class="right-text">500Kw</em>
            </div>
            <div class="progress-box effective-power-box">
              <el-progress :percentage="60"
                           color="#ec808d"></el-progress>
              <p>有效功率</p>
            </div>
            <div class="progress-box invalid-power-box">
              <el-progress :percentage="10"
                           color="#caf982"></el-progress>
              <p>无效功率</p>
            </div>
          </div> -->
