<script>
import { mapActions } from 'vuex'

export default {
  name: 'projectMonitoringDetail',
  data() {
    return {
      firstRequest: true,
      crewList: [],
      myCharts: [],
      animation: null,
      option_1: {
        grid: {
          top: '16px',
          right: '16px',
          bottom: '16px',
          left: '16px',
          containLabel: true
        },
        series: [
          {
            type: 'gauge',
            radius: '90%',
            min: 0,
            max: 2000,
            // splitNumber: 20,
            axisLine: {
              lineStyle: {
                width: 5,
                color: [
                  [0.3, '#67e0e3'],
                  [0.7, '#37a2da'],
                  [1, '#fd666d']
                ]
              }
            },
            pointer: {
              width: 2,
              itemStyle: {
                color: 'auto'
              }
            },
            axisTick: {
              distance: -5,
              length: 2,
              lineStyle: {
                color: '#fff',
                width: 1
              }
            },
            splitLine: {
              distance: -5,
              length: 5,
              lineStyle: {
                color: '#fff',
                width: 2
              }
            },
            axisLabel: {
              color: '#0099FF',
              distance: 8,
              fontSize: 8,
              formatter: function (v) {
                return v.toFixed(0)
              }
            },
            detail: {
              valueAnimation: true,
              formatter: ' {value} RPM',
              color: 'auto',
              fontSize: 10,
              width: 60,
              lineHeight: 16,
              overflow: 'break',
              offsetCenter: [0, '40%']
            },
            data: [
              {
                value: 0,
                name: '',
                title: {
                  fontSize: 10,
                  offsetCenter: ['0%', '40%']
                }
              }
            ]
          }
        ]
      },
      // 遮罩
      loading: true,
      resizefun: null
    }
  },
  computed: {
    targetStatusText(val) {
      const statusEnumList = ['故障', '停机', '怠速', '额定转速', '合闸待并机']
      return function (val) {
        return statusEnumList[val]
      }
    }
  },
  mounted() {
    this.getMchInfo()
    // this.$nextTick(() => {
    //   this.loading = false
    // })
    setTimeout(() => {
      this.loading = false
    }, 500)
    this.animation = setInterval(() => {
      this.getMchInfo()
    }, 1000 * 5 * 1)

    this.resizefun = () => {
      this.myCharts.forEach((item) => {
        item.resize()
      })
    }
    window.addEventListener('resize', this.resizefun)
  },
  beforeDestroy() {
    clearInterval(this.animation)
    window.removeEventListener('resize', this.resizefun)
    this.resizefun = null
  },
  methods: {
    ...mapActions(['toggleSelectCrew']),
    // 查询详细数据
    getMchInfo() {
      // this.$api.screenData
      //   .getsingleMchInfoRealtime({ id: this.selectCrew.ID })
      //   .then((res) => {

      this.crewList = [
        {
          ID: 4152,
          status: 4,
          value: 500,
          targetStatus: '4' // 机组当前状态
        },
        {
          ID: 5008,
          status: 3,
          value: 500,
          targetStatus: '0' // 机组当前状态
        },
        {
          ID: 5229,
          status: 2,
          value: 500,
          targetStatus: '1' // 机组当前状态
        },
        {
          ID: 6052,
          status: 6,
          value: 500,
          targetStatus: '2' // 机组当前状态
        },
        {
          ID: 6229,
          status: 6,
          value: 500,
          targetStatus: '3' // 机组当前状态
        },
        {
          ID: 6455,
          status: 6,
          value: 500,
          targetStatus: '4' // 机组当前状态
        }
      ]
      // 第一次请求 初始化图例
      if (this.firstRequest) {
        this.$nextTick(() => {
          this.init()
        })
      } else {
        // 刷新图例
        this.refreshData()
      }
      // })
    },
    // 图例初始化
    init() {
      this.crewList.forEach((item) => {
        const chartDom = document.getElementById(`crew-speed-echart-${item.ID}`)
        const myChart = this.$echarts.init(chartDom)
        // this.option_1.series[0].min = item.min
        // this.option_1.series[0].max = item.max
        this.option_1.series[0].data[0].value = item.value
        myChart.setOption(this.option_1)
        this.myCharts.push(myChart)
      })
    },
    // 图例刷新
    refreshData() {
      this.crewList.forEach((item, index) => {
        // this.option_1.series[0].min = item.min
        // this.option_1.series[0].max = item.max
        this.option_1.series[0].data[0].value = item.value // this.realTimeData[item.key]
        this.myCharts[index].setOption(this.option_1)
      })
    },
    linkTo(crewId, path) {
      // 切换机组
      this.$api.screenData.getsingleMchInfo({ id: crewId }).then((value) => {
        const data = value.Data
        data && this.toggleSelectCrew(data)
        this.$router.push(path)
      })
    }
  }
}
</script>

